.filters_button {
  display: flex;
  align-items: center;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
}
.filters_button_icon {
  display: inline-block;
  margin-right: 5px;
  height: 18px;
  width: 18px;
}
.filters-popover .ant-popover-arrow:before {
  background-color: rgba(255,255,255,0.1);
}
.filters-popover .ant-popover-inner {
  background: rgba(255,255,255,0.1);
}
.filters-select-label {
  min-width: 180px;
  margin: 0px 0 4px;
  font-size: 14px;
  color: #fff;
  font-weight: 300;
}
.filters-popover .ant-select-filled .ant-select-selector,.filters-popover .ant-select-filled.ant-select-focused .ant-select-selector, .filters-popover .ant-select-filled.ant-select-open .ant-select-selector {
  background: #374174 !important;
  border-radius: 6px;
  color: #fff !important;
  font-weight: 500;
  font-size: 14px;
  line-height: 26px;
  border:none;
  cursor: pointer;
  padding: 3px 19px;
}
.filters-popover .ant-select-selection-item{
  color: #fff !important;
}
.filters-popover .ant-select-filled:hover .ant-select-selector {
  background: #303862 !important;
}
.filters-popover .ant-select-filled .ant-select-selector.ant-select-focused {
  box-shadow: none;
  color: #fff;
}
