.home-title {
    color:#fff;
    font-size: 64px;
    line-height: 82px;
    font-weight: 200;
    margin-top: 0;
}
.home-title-accent {
    font-weight: 600;
}
.home-navigation {
    display: flex;
    justify-content: flex-start;
    margin-top: 50px;
    max-width: 400px;
    flex-wrap: wrap;
}
.home-navigation-link {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 110px;
    height: 110px;
    color: #fff;
    background: rgba(255,255,255,.1);
    border-radius: 18px;
    font-size: 18px;
    line-height: 29px;
    transition: 200ms ease-in all;
    margin: 5px;
}
.home-navigation-link:hover {
    color: #fff;
    background: rgba(255,255,255,0.2);
}
.home-navigation-link:active {
    background: rgba(255,255,255,0.3);
}
.home-navigation-link_icon {
    max-height: 22px;
    margin-bottom: 6px;
}
.home-sponsors {
    display: flex;
    margin-top: 50px;
    justify-content: flex-start;
}
.home-sponsors-image {
    max-height: 80px;
    margin-right: 24px;
}

.home-slide-image {
    min-height: 310px;
    background-size: cover;
    border-radius: 18px;
    margin-bottom: 50px;
}
.home-slide-title {
    font-weight: 300;
    color: #fff;
    font-size: 22px;
    text-align: center;
    line-height: 32px;
    margin-left: 50px;
    margin-right: 50px;
    margin-bottom: 50px;
}

.home-slide-text {
    font-weight: 300;
    color: #fff;
    font-size: 14px;
    text-align: center;
    line-height: 20px;
}
button.slick-arrow.slick-prev {
    left: 0;
    top: 380px;
}
button.slick-arrow.slick-next {
    right: 0;
    top: 380px;
}

.ant-carousel .slick-prev::after, .ant-carousel .slick-next::after {
    box-sizing: border-box;
    position: absolute;
    top: calc((16px - 16px / 1.414) / 2);
    inset-inline-start: calc((16px - calc(16px / 1.414)) / 2);
    display: inline-block;
    width: calc(16px / 1.414);
    height: calc(16px / 1.414);
    border: 0 solid #fff;
    border-inline-width: 2px 0;
    border-block-width: 2px 0;
    border-radius: 1px;
    content: "";
}
.ant-carousel .slick-prev::after {
    transform: rotate(-45deg);
}
.ant-carousel .slick-next::after {
    transform: rotate(135deg);
}