.App {
  background-image:
  url('assets/bg.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  min-height: 100vh;
}
.App::after {
  content: '';
  display: block;
  position: absolute;
  top:0;
  bottom:0;
  left:0;
  right: 0;
  backdrop-filter: blur(22px);
  z-index: 0;
}

.full-height {
  height: 100%;
}
.root_header {
  display: flex;
  align-items: center;
}

.chart_tick_text {
  font-size: 14px;
  font-size: 500;
  color:#fff;
}